export function addChart() {
    const full_width = window.innerWidth - 35
    const full_height = window.innerHeight - 110;

    const margin = { top: 50, right: 50, bottom: 100, left: 50 }
    const width = full_width - margin.left - margin.right
    const height = full_height - margin.top - margin.bottom
        // console.log("Size " + [width, height])

    const svg = d3
        .select("#BubbleChartSection")
        .append("svg")
        .attr("id", "svgBubble")
        // .attr("viewBox", [0, 0, width, height])
        .attr("width", width + margin.left + margin.right)
        .attr("height", height + margin.top + margin.bottom)
        // .call(d3.zoom().on("zoom", function () {
        //   svg.attr("transform", d3.event.transform)
        //   resimulate()
        // }))
        .append("g")
        .attr("transform",
            "translate(" + margin.left + "," + margin.top + ")");

    tip = d3Tip().attr('class', 'd3-tip').html(function(d) { return tooltip.getProductCard(d) })
    tip.direction(function(d) {
        var upper = d.disponibility > 0.5
        var left = xScale(d.prix) < (0.5 * xScale.range()[1])
        return (upper ? 's' : 'n') + (left ? 'e' : 'w')
    })

    svg.call(tip)

    const data = Object.values(preprocess.getProduits())

    yScale = d3.scaleLinear().domain([0, 1]).range([height, 0]).nice();
    // console.log(extent(data, (d) => d.prix))
    xScale = d3
        .scaleLog()
        // .domain([d3.min(data, function (d) { return d.prix; }), 1000])
        .domain([5, 150]) //d3.extent(data, (d) => d.prix))
        .range([0, width])
        // .nice()
    xScale.clamp(true)
    colorScale = preprocess.getColorScale1();


    legend.drawLegend(colorScale, build, svg, width, height)

    helper.appendAxes(svg)
        // helper.drawXAxis(xScale, height)
    xAxis = d3.select('.x.axis').attr('transform', 'translate( 0, ' + (height + 30) + ')')
        .call(d3.axisBottom(xScale).tickSizeOuter(5).tickFormat(d3.format(",.2f"))) //.tickArguments([8, '~s']))
    yAxis = d3.select('.y.axis')
        .call(d3.axisLeft(yScale).tickSizeOuter(0).tickArguments([5, '.0r']).tickFormat(d3.format(",.3p")))
        // helper.drawYAxis(yScale)

    // Axis
    helper.appendGraphLabels(svg)
    svg.selectAll('.x.axis-text').attr('transform', 'translate(' + width / 2 + ', ' + (height + 60) + ')')
    svg.selectAll('.y.axis-text').attr('transform', 'translate(' + -margin.left / 2 + ', ' + height / 2 + ') rotate(-90)')

    // Add a clipPath: everything out of this area won't be drawn.
    let clip = svg.append("defs").append("SVG:clipPath")
        .attr("id", "clipBubble")
        .append("SVG:rect")
        .attr("width", width)
        .attr("height", height - 300)
        .attr("x", 0)
        .attr("y", 0);

    svg.append('g')
        .attr("clip-path", "url(#clip)")
        .attr("id", "bubbleChart")

    const maxRange = (width * height) / 100000
        // console.log("Max dot radius " + maxRange)
    sizeScale = d3.scaleLog().domain([1, d3.max(data.map((d) => d.total))]).range([1, maxRange]);
    sizeScale.clamp(true)

    // let zoom = d3.zoom()
    //   .scaleExtent([1, 40])
    //   .translateExtent([[0, 0], [width, height]])
    //   .on("zoom", zoomed);

    // svg.call(zoom);

    // var view = svg.append("rect")
    //   .attr("class", "view")
    //   .attr("x", 0.5)
    //   .attr("y", 0.5)
    //   .attr("opacity", 0)
    //   .attr("width", width - 1)
    //   .attr("height", height - 1);

    // function zoomed() {
    //   // view.attr("transform", d3.event.transform);
    //   var newX = d3.event.transform.rescaleX(xScale);
    //   var newY = d3.event.transform.rescaleY(yScale);

    //   // update axes with these new boundaries
    //   xAxis.call(d3.axisBottom(newX).tickFormat(d3.format(",.2f")))
    //   yAxis.call(d3.axisLeft(newY).tickFormat(d3.format(",.3p")))

    //   d3.select("#bubbleChart")
    //     .selectAll(".productCircle")
    //     .attr("cx", (d) => newX(d.prix))
    //     .attr("cy", (d) => newY(d.disponibility))

    //   // resimulate()
    // }

    // function resetted() {
    //   svg.transition()
    //     .duration(750)
    //     .call(zoom.transform, d3.zoomIdentity);

    // }

    viz.addBubbleUI(build, updateChart)
    build(preprocess.getTopProduits([1, 3000]))

    // function addSuccSelect() {
    //     const tempSucc = ["Toutes"].concat(preprocess.getSuccursales().sort(helper.compareNom))
    //     d3.select("#selectSucc")
    //         .selectAll()
    //         .data(tempSucc)
    //         .enter()
    //         .append('option')
    //         .text(function(d) { return d.nom ? d.nom : tempSucc[0]; }) // text showed in the menu
    //         .attr("value", function(d) { return d.id ? d.id : 0; }) // corresponding value returned by the button

    //     d3.select("#selectSucc-button")
    //         .selectAll("span")
    //         .text("Filtrer par succursale")

    //     // When the button is changed, run the updateChart function
    //     d3.select("#selectSucc").on("change", function(d) {
    //         updateOpacity()
    //     })
    // }

    function build(productsToShow) {
        if (simulation) {
            simulation.stop()
        }
        if (!productsToShow) {
            productsToShow = getBubbleChartContent()
            console.log("done w/ content process")
        }

        d3.select("#bubbleChart")
            .selectAll(".productCircle")
            .data(productsToShow)
            .join("circle")
            .attr("class", "productCircle")
            .attr("stroke", "black")
            .attr("stroke-width", "0.4")
            .attr("fill", (d) => colorScale(d.Cat1))
            .attr("r", (d) => sizeScale(d.total))
            .attr("cx", (d) => xScale(d.prix))
            .attr("cy", (d) => yScale(d.disponibility))
            .on('mouseover', function(d) {
                d3.select(this).attr("stroke-width", "2.5")
                tip.show(d, this)
            })
            .on('mouseout', function() {
                d3.select(this).attr("stroke-width", "0.4")
                tip.hide()
            })
            .on('click', function(d) {
                window.open(
                    'https://www.saq.com/fr/' + d.id,
                    '_blank' // in a new tab
                );
            })

        if (USE_SIM) {
            simulation = d3
                .forceSimulation(productsToShow)
                .force("x", d3.forceX((d) => {
                    return xScale(d.prix);
                }).strength(0.2))
                .force("y", d3.forceY(function(d) {
                    return yScale(d.disponibility);
                }).strength(0.4))
                .force("collide", d3.forceCollide((d) => {
                    return 0.5 * sizeScale(d.total);
                }))
                .alphaDecay(0)
                .alpha(0.4)
                .on("tick", tick);

            function tick() {
                d3.selectAll(".productCircle")
                    .attr("cx", (d) => {
                        return d.x;
                    })
                    .attr("cy", (d) => d.y);
            }
            resimulate()
        }
        updateOpacity()
        updateChart()
    }
}

function resimulate() {
    console.log("simulate")
    if (init_decay) {
        clearTimeout(init_decay)
    }
    if (stop_sim) {
        clearTimeout(stop_sim)
    }
    init_decay = setTimeout(function() {
        // console.log("start alpha decay");
        simulation.alphaDecay(0.1);
    }, 8000);

    stop_sim = setTimeout(function() {
        // console.log("stop sim");
        simulation.stop();
    }, 12000);
}


function getBubbleChartContent() {
    const regionName = d3.select("#bubble-region").property('value')
    const succName = d3.select("#bubble-branch").property('value')
    const filter = d3.select("#selectFilter").property('value')
    const lowerRange = d3.select("#rangeStart").property('value')
    const upperRange = d3.select("#rangeEnd").property('value')
    return preprocess.getFilteredProducts(regionName, succName, filter, lowerRange, upperRange)
}

function updateOpacity() {
    d3.selectAll(".productCircle")
        .attr("opacity", (d) => (d.subTotal > 0 ? 1 : 0.1));
}

function updateChart() {
    const newData = getBubbleChartContent()
    const dictionary = Object.assign({}, ...newData.map((x) => ({
        [x.id]: x
    })));
    d3.select("#bubbleChart")
        .selectAll(".productCircle").each(function(d) {
            d.subTotal = dictionary[d.id].subTotal
            d.SuccDispoCount = dictionary[d.id].SuccDispoCount
        })
    updateOpacity()
}